<template>
  <div class="container">
    <el-form :inline="true">
      <el-form-item>
        <el-input
            v-model="searchForm.username"
            placeholder="用户名"
            clearable
        >
        </el-input>
      </el-form-item>

      <el-form-item>
        <el-button @click="getUserList">搜索</el-button>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="dialogVisible = true" v-if="hasAuth('sys:user:save')">新增</el-button>
      </el-form-item>
      <el-form-item>
        <el-popconfirm title="这是确定批量删除吗？" @confirm="delHandle(null)">
          <el-button type="danger" slot="reference" :disabled="delBtlStatu" v-if="hasAuth('sys:user:delete')">批量删除</el-button>
        </el-popconfirm>
      </el-form-item>
    </el-form>
    <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        border
        stripe
        @selection-change="handleSelectionChange">

      <el-table-column
          type="selection"
          width="60">
      </el-table-column>

      <el-table-column
          prop="id"
          label="ID"
          width="60">
      </el-table-column>

      <el-table-column
          prop="nodeType"
          label="节点类型">
      </el-table-column>

      <el-table-column
              prop="defaultName"
              label="节点名称">
      </el-table-column>

      <el-table-column
          prop="parentId"
          label="父节点ID">
      </el-table-column>

      <el-table-column
          prop="fieldInfoId"
          label="节点信息"
          width="400">
      </el-table-column>

      <el-table-column
          prop="icon"
          width="260px"
          label="操作">

        <template slot-scope="scope">
          <!--          <el-button type="text" @click="view(scope.row)" v-if="hasAuth('sys:field:view')">查看</el-button>-->
          <el-button type="text" @click="view(scope.row)" >查看</el-button>
          <el-divider direction="vertical"></el-divider>

          <!--          <el-button type="text" @click="editHandle(scope.row.id)" v-if="hasAuth('sys:field:update')">编辑</el-button>-->
          <el-button type="text" @click="editHandle(scope.row.id)" >编辑</el-button>
          <el-divider direction="vertical"></el-divider>

          <template>
            <!--            <el-popconfirm title="这是一段内容确定删除吗？" @confirm="delHandle(scope.row.id)" v-if="hasAuth('sys:field:delete')">-->
            <el-popconfirm title="这是一段内容确定删除吗？" @confirm="delHandle(scope.row.id)" >
              <el-button type="text" slot="reference">删除</el-button>
            </el-popconfirm>
          </template>

        </template>
      </el-table-column>

    </el-table>

    <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 20, 50, 100]"
        :current-page="current"
        :page-size="size"
        :total="total">
    </el-pagination>

    <!--新增对话框-->
    <el-dialog
        title="新增字段树"
        :visible.sync="dialogVisible"
        width="600px"
        :before-close="handleClose">

      <el-form :model="editForm" :rules="editFormRules" ref="editForm">

        <el-form-item label="字段类型" :rules="editFormRules" label-width="100px">
          <el-select v-model="editForm.nodeType" placeholder="请选择字段类型" >
            <el-option label="string" value="string"></el-option>
            <el-option label="int" value="int"></el-option>
            <el-option label="long" value="long"></el-option>
            <el-option label="float" value="float"></el-option>
            <el-option label="array" value="array"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="父字段ID" prop="parentId" label-width="105px" >
          <el-input-number v-model="editForm.parentId" @change="handleChange" :min="0" :max="10000" ></el-input-number>
        </el-form-item>
        <el-form-item label="字段信息ID" prop="fieldInfoId" label-width="105px" >
          <el-input-number v-model="editForm.fieldInfoId" @change="handleChange" :min="0" :max="10000"></el-input-number>
        </el-form-item>

        <el-form-item label="默认名称"  prop="defaultName" label-width="105px">
          <el-input v-model="editForm.defaultName" autocomplete="off"></el-input>
        </el-form-item>

      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('editForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('editForm')">确 定</el-button>
      </div>
    </el-dialog>

    <!--查看对话框-->

  </div>

</template>

<script>
import { treelist } from '@/api/field'
import TreeItem from '../../components/TreeItem'
export default {
  name: "FieldTree",
  components: {
    TreeItem
  },
  data() {
    return {
      treeData: [{
        name: '1',
        child: [
          { name: '2',
            child: [{ name: '1' }, { name: '2' }]
          },
          { name: '1',
            child: [{ name: '1' }, { name: '2' }]
          }
        ]
      }],
      searchForm: {},
      delBtlStatu: true,

      total: 0,
      size: 10,
      current: 1,

      dialogVisible: false,
      editForm: {

      },

      tableData: [],

      editFormRules: {
        nodeType: [
          {required: true, message: '请输入节点类型', trigger: 'blur'}
        ],
        parentId: [
          {required: true, message: '请输入父字段ID', trigger: 'blur'}
        ],
        fieldInfoId: [
          {required: true, message: '请输入字段信息ID', trigger: 'blur'}
        ],
        defaultName: [
          {required: true, message: '请输入默认名称', trigger: 'blur'}
        ]
      },

      multipleSelection: [],

      roleDialogFormVisible: false,
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      roleForm: {},
      roleTreeData:  [],
      treeCheckedKeys: [],
      checkStrictly: true
    }
  },
  created() {
    this.getUserList()

    this.$axios.get("/sys/role/list").then(res => {
      this.roleTreeData = res.data.data.records
    })
  },
  methods: {
    // 拖拽移动
    move(e) {
      const odiv = e.currentTarget // 获取元素
      console.log(odiv)
      // 算出鼠标相对元素的位置
      const disX = e.clientX - odiv.offsetLeft
      const disY = e.clientY - odiv.offsetTop
      document.onmousemove = (e) => { // 鼠标按下并移动的事件
        // 用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
        const left = e.clientX - disX
        const top = e.clientY - disY
        // 绑定元素位置到positionX和positionY上面
        this.positionX = top
        this.positionY = left
        // 移动当前元素
        odiv.style.left = left + 'px'
        odiv.style.top = top + 'px'
      }
      document.onmouseup = () => {
        document.onmousemove = null
        document.onmouseup = null
      }
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      console.log("勾选")
      console.log(val)
      this.multipleSelection = val;

      this.delBtlStatu = val.length == 0
    },

    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.size = val
      this.getUserList()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.current = val
      this.getUserList()
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.dialogVisible = false
      this.editForm = {}
    },
    handleClose() {
      this.resetForm('editForm')
    },

    getUserList() {
      this.$axios.get("/sys/user/list", {
        params: {
          username: this.searchForm.username,
          current: this.current,
          size: this.size
        }
      }).then(res => {
        this.tableData = res.data.data.records
        this.size = res.data.data.size
        this.current = res.data.data.current
        this.total = res.data.data.total
      })
    },

    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$axios.post('/sys/user/' + (this.editForm.id?'update' : 'save'), this.editForm)
              .then(res => {

                this.$message({
                  showClose: true,
                  message: '恭喜你，操作成功',
                  type: 'success',
                  onClose:() => {
                    this.getUserList()
                  }
                });

                this.dialogVisible = false
              })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    editHandle(id) {
      this.$axios.get('/sys/user/info/' + id).then(res => {
        this.editForm = res.data.data

        this.dialogVisible = true
      })
    },
    delHandle(id) {

      var ids = []

      if (id) {
        ids.push(id)
      } else {
        this.multipleSelection.forEach(row => {
          ids.push(row.id)
        })
      }

      console.log(ids)

      this.$axios.post("/sys/user/delete", ids).then(res => {
        this.$message({
          showClose: true,
          message: '恭喜你，操作成功',
          type: 'success',
          onClose:() => {
            this.getUserList()
          }
        });
      })
    },

    roleHandle (id) {
      this.roleDialogFormVisible = true

      this.$axios.get('/sys/user/info/' + id).then(res => {
        this.roleForm = res.data.data

        let roleIds = []
        res.data.data.sysRoles.forEach(row => {
          roleIds.push(row.id)
        })

        this.$refs.roleTree.setCheckedKeys(roleIds)
      })
    },
    submitRoleHandle(formName) {
      var roleIds = this.$refs.roleTree.getCheckedKeys()

      console.log(roleIds)

      this.$axios.post('/sys/user/role/' + this.roleForm.id, roleIds).then(res => {
        this.$message({
          showClose: true,
          message: '恭喜你，操作成功',
          type: 'success',
          onClose:() => {
            this.getUserList()
          }
        });

        this.roleDialogFormVisible = false
      })
    },
    repassHandle(id, username) {

      this.$confirm('将重置用户【' + username + '】的密码, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios.post("/sys/user/repass", id).then(res => {
          this.$message({
            showClose: true,
            message: '恭喜你，操作成功',
            type: 'success',
            onClose: () => {
            }
          });
        })
      })
    }
  }
}
</script>

<style scoped>
.container{
  position: relative;
  width: 100%;
}

.el-pagination {
  float: right;
  margin-top: 22px;
}

.tree{
  background: #fff;
  min-height: calc(100vh - 110px);
  position: relative;
}

.tree-content{
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
}
</style>